import { get } from 'svelte/store';
import { MessageConstants } from '../MessageConstants';
import { trainsById } from '../../../stores/trains';
import TrainDetails from '../../TrainDetails';
import { platformsByName } from '../../../stores/platforms';
import PubSub from '../../pub_sub/PubSub';
import TrainDetailsChangedEvent from '../../pub_sub/events/train/details/TrainDetailsChangedEvent';
export default class TrainDetailsProcessor {
    getName() {
        return MessageConstants.TRAIN_DETAILS;
    }
    process(data) {
        const trains = get(trainsById);
        const train = trains.get(Number(data.zid));
        const isVisible = data.sichtbar === 'true';
        const isAtPlatform = data.amgleis === 'true';
        const platforms = get(platformsByName);
        const details = new TrainDetails(Number(data.verspaetung), platforms.get(data.gleis), platforms.get(data.plangleis), data.von, data.nach, isVisible, isAtPlatform);
        train.details = details;
        PubSub.publish(new TrainDetailsChangedEvent(train, details));
    }
}
