export default class TrainStop {
    constructor(arrival, departure, platform, plannedPlatform, rawFlags) {
        this.arrival = arrival;
        this.departure = departure;
        this.platform = platform;
        this.plannedPlatform = plannedPlatform;
        this.rawFlags = rawFlags;
    }
    setSuccessorId(v) {
        this.successorId = v;
    }
    getSuccessorId() {
        return this.successorId;
    }
    set isPassing(v) {
        this._isPassing = v;
    }
    get isPassing() {
        return this._isPassing;
    }
    hasPredecessor() {
        return this.predecessor !== undefined;
    }
    hasSuccessor() {
        return this.successorId !== undefined;
    }
}
