import { writable, derived } from 'svelte/store';
export const trains = writable([]);
export const trainsById = derived(trains, ($trains) => {
    const _trains = new Map();
    $trains.forEach((t) => {
        _trains.set(Number(t.id), t);
    });
    return _trains;
});
export const trainsToShow = writable([]);
